.shape {
  background-color: #222;

  &__container {
  }

  &__content {
    display: flex;

    min-height: 535px;

    @media (max-width: 1000px) {
      margin: 0 auto;

      width: fit-content;

      flex-direction: column;
      text-align: center;
    }
  }

  &__content-left {
    padding: 50px 35px;

    min-width: 454px;

    display: flex;
    flex-direction: column;

    border-radius: 30px;
    background-color: #fff;

    @media (max-width: 1000px) {
      padding: 50px 35px 25px;

      min-width: 0;

      align-items: center;

      border-radius: 30px 30px 0 0;
    }
  }

  &__content-right {
    position: relative;

    padding: 60px 35px 50px 65px;
    width: 100%;

    display: flex;
    flex-direction: column;

    border-radius: 30px;
    background-color: #fff;

    @media (max-width: 1000px) {
      padding: 25px 35px 50px;

      border-radius: 0 0 30px 30px;
    }
  }
}

.shape__content-right::after {
  content: "";
  position: absolute;
  top: 35px;
  left: 0;
  transform: translate(-50%);
  width: 10px;
  height: 460px;
  background-image: url(".././img/form-hr.svg");

  @media (max-width: 1000px) {
    display: none;
  }
}

.form {
  margin-top: 125px;

  display: flex;
  flex-direction: column;
  flex: 1;

  @media (max-width: 1000px) {
    margin-top: 60px;

    align-items: center;
  }

  @include mobile {
    margin-top: 45px;
  }

  &__input {
    position: relative;
    display: inline-block;

    width: 100%;
    font-weight: 600;
    font-size: 24px;
    color: rgba(0, 0, 0, 0.5);
    border-bottom: 2px solid rgba(0, 0, 0, 0.5);

    &:not(:nth-child(2)) {
      margin-bottom: 70px;
    }

    @include mobile {
      padding-bottom: 4px;

      font-size: 14px;
    }
  }

  &__btn {
    display: flex;
    margin-top: auto;
    padding: 12px 75px;

    max-width: 240px;

    font-weight: 600;
    font-size: 25px;
    text-align: center;
    color: #fff;
    border-radius: 20px;
    background-color: #ff671b;

    @media (max-width: 1000px) {
      margin-top: 25px;
    }

    @include mobile {
      padding: 12px 58px;

      border-radius: 9px;
      font-size: 16px;
    }
  }
}

