.price {
  &__title {
    margin: 0;
    margin-bottom: 55px;

    font-weight: 600;
    font-size: 49px;
    color: #000;
    text-align: center;

    @media (max-width: 1000px) {
      margin-bottom: 37px;
    }

    @include mobile {
      font-size: 25px;
    }
  }

  &__value {
    margin: 0;
  }

  &__value-new {
    margin-bottom: 22px;
    padding: 10px 25px 10px 50px;

    font-weight: 600;
    font-size: 75px;
    color: #fff;
    border-radius: 50px;
    background-color: #598be0;

    @media (max-width: 1000px) {
      margin-bottom: 12px;
      padding: 7px 22px;

      width: 100%;
      max-width: 300px;

      font-size: 41px;
    }
  }

  &__value-old {
    padding-left: 50px;

    font-weight: 600;
    font-size: 40px;
    text-decoration: line-through;
    color: rgba(0, 0, 0, 0.7);

    @media (max-width: 1000px) {
      margin-bottom: 25px;
      padding-left: 0;
    }

    @include mobile {
      font-size: 29px;
    }
  }

  &__discount {
    margin-top: auto;
    padding: 13px 21px;

    font-weight: 600;
    font-size: 22px;
    text-align: center;
    color: #ff671b;
    border-radius: 20px;
    background-color: #fff0e9;

    @include mobile {
      padding: 10px 15px;

      font-size: 15px;
    }
  }
}

.fields {
  &__title {
    margin: 0;

    font-weight: 600;
    font-size: 34px;
    color: #000;

    @include mobile {
      font-size: 20px;
    }
  }
}
