.services {
  &__container {
  }

  &__subtitle {
    margin: 0;
    margin-bottom: 90px;

    @include mobile {
      margin-bottom: 60px;
    }
  }

  &__content {
    display: flex;

    @media (max-width: 1020px) {
      margin: 0 auto;

      width: fit-content;

      flex-direction: column;
    }
  }

  &__content-left {
    position: relative;

    width: 50%;

    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    gap: 75px;

    border: 1px solid #c6c6c6;
    border-radius: 16px;
    background-color: #eaebed;

    @media (max-width: 1020px) {
      padding: 70px 50px;

      width: inherit;

      border: 2px solid #c6c6c6;
    }

    @media (max-width: 620px) {
      border: 2px solid #c6c6c6;
    }

    @include mobile {
      padding: 45px;

      gap: 15px;
    }
  }

  &__content-left::before {
    content: "";
    position: absolute;
    top: -8px;
    left: 0;
    transform: translate(0%);
    width: 100%;
    height: 50px;
    z-index: -1;
    border-radius: 16px;
    background-color: #598be0;
  }

  &__tabs {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    transform: translateY(-47%);
    z-index: -1;
  }

  &__text {
    margin: 0;

    font-weight: 400;
    font-size: 18px;
    color: #000;

    @include mobile {
      font-size: 16px;
      letter-spacing: -0.02em;
    }
  }

  &__text-up {
    padding: 25px;

    max-width: 390px;

    border-radius: 10px;
    background-color: #fff;

    @include mobile {
      padding: 20px 14px;
    }
  }

  &__bold {
    font-weight: 600;
    font-size: 20px;
    line-height: 125%;
    letter-spacing: -0.03em;
    color: #131313;

    @include mobile {
      font-size: 16px;
      letter-spacing: -0.02em;
    }
  }

  &__hr {
    margin: 0;

    width: 100%;
    max-width: 440px;

    border-color: #fff;
  }

  &__text-down {
    padding: 25px;

    max-width: 390px;

    @include mobile {
      padding: 20px 14px;
    }
  }

  &__content-right {
    width: 50%;

    border: 1px solid #dadada;
    border-radius: 16px;

    @media (max-width: 1020px) {
      width: 100%;

      border: 2px solid #c6c6c6;
    }
  }

  &__list {
    padding: 30px 0;

    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-between;
    gap: 50px;

    :not(:last-of-type) {
      border-bottom: 1px solid #dadada;
    }

    @include mobile {
      padding: 15px;
    }
  }

  &__item {
    padding-bottom: 32px;
    min-width: 450px;
    display: flex;
    align-items: center;
    gap: 40px;

    @include mobile {
      padding-bottom: 15px;

      width: 100%;
      max-width: 450px;
      min-width: 0;
      justify-content: center;

      gap: 15px;
    }
  }

  &__circle {
    width: 40px;
    height: 40px;

    display: flex;
    align-items: center;
    justify-content: center;

    border-radius: 50%;
    background: #598be0;

    @include mobile {
      width: 22px;
      height: 22px;
    }
  }

  &__image {
    @include mobile {
      width: 12px;
      height: 12px;
    }
  }

  &__text--list {
    max-width: 370px;

    @include mobile {
      font-size: 15px;
      color: #000;
    }

    @media (max-width: 475px) {
      max-width: 300px;
    }

    @media (max-width: 410px) {
      max-width: 210px;
    }
  }
  &__text-bold {
    @include mobile {
      font-weight: 600;
    }
  }
}
