.hero {
  height: 100vh;
  position: relative;

  background-color: #000;
  z-index: 2;

  @include mobile {
    background: url("./../img/hero-mobile.png") center center / cover no-repeat;
    background-size: 95%;
    background-color: #000;
  }

  &__container {
  }

  &__content {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);

    width: 100%;
  }

  &__title {
    margin: 0 auto;

    max-width: 863px;

    font-weight: 600;
    font-size: 46px;
    line-height: 124%;
    text-align: center;
    color: #fff;

    @include mobile {
      font-size: 28px;
      line-height: 125%;
    }

    @media (max-width: 375px) {
      font-size: 25px;
    }
  }

  &__title--span {
    padding: 1px 9px;

    color: rgba(0, 0, 0, 0.8);
    background-color :rgba(255,255,255, 0.7);
    border-radius: 20px;
    // filter: brightness(0.5);
    // filter: alpha(opacity=40);
    // opacity: 0.4;
  }

  &__inner {
    position: relative;

    max-width: 1200px;

    @include flex-all-sb;

    margin: 36px auto 0;
    padding: 0px var(--container-offset);

    font-weight: 400;
    font-size: 16px;

    @media (max-width: 830px) {
      margin: 100px auto 0;
      gap: 50px 100px;
      flex-wrap: wrap;
    }

    @media (max-width: 510px) {
      gap: 50px 18px;
    }

    @media (max-width: 370px) {
      justify-content: center;
      text-align: center;
      gap: 25px;
    }
  }

  &__text {
    margin: 0;

    color: #fff;

    @include mobile {
      font-size: 14px;
    }
  }

  &__text--left {
    max-width: 255px;

    @include mobile {
      max-width: 195px;
    }
  }

  &__text--right {
    max-width: 125px;
  }

  &__btn {
    position: absolute;
    padding: 18px 81px;

    top: 50%;
    left: 50%;
    transform: translate(-50%, -20%);
    background-color: #fff;
    border-radius: 10px;
    color: #000;

    transition: 0.3s ease-in-out;

    @include hover {
      background-color: rgb(243, 243, 243);
    }

    @media (max-width: 830px) {
      position: static;

      margin: 0 auto;
      transform: none;

      order: 3;
    }
  }
}

#background-video {
  width: 100%;
  height: 100%;
  object-fit: cover;
  position: absolute;
  left: 0;
  right: 0;
  top: 0;
  bottom: 0;
  z-index: -1;

  @include mobile {
    display: none;
  }
}
