.advantages {
  background-color: #222;

  &__container {
  }

  &__subtitle {
    margin: 0;
    margin-bottom: 50px;

    color: #fff;
  }

  &__content {
    @media (max-width: 1000px) {
      margin: 0 auto;

      width: fit-content;
    }
  }

  &__list {
    display: grid;
    grid-template-columns: repeat(4, 1fr);
    column-gap: 13px;
    row-gap: 8px;

    @media (max-width: 1000px) {
      display: flex;
      flex-direction: column;
    }
  }

  &__item {
    display: flex;
    grid-column: 1 span;

    background: #fff;
    border-radius: 10px;

    @media (max-width: 1000px) {
      padding: 50px 30px;
    }
  }

  &__item--big {
    grid-column: 2 span;
    grid-column-start: 2;
    grid-column-end: 4;

    grid-row: 2 span;
  }

  &__item--full {
    grid-column: 4 span;
  }

  &__image {
    padding: 90px 23px;

    width: 100%;

    @media (max-width: 1000px) {
      margin: 0 auto;
      padding: 0;

      max-width: fit-content;
    }
  }
}

.item {
  &__card {
    margin: 0 auto;

    width: 100%;
    max-width: 235px;

    display: flex;
    flex-direction: column;
    justify-content: center;
    text-align: center;
    gap: 15px;
  }

  &__card--full {
    margin: 0 auto;
    padding: 44px 0;

    max-width: 910px;

    display: flex;
    align-items: center;
    gap: 70px;

    @media (max-width: 1000px) {
      padding: 0;

      max-width: 500px;

      display: flex;
      flex-direction: column;
      text-align: center;
      gap: 25px;
    }
  }

  &__name {
    font-weight: 600;
    font-size: 18px;
    color: #598be0;
  }

  &__descr {
    margin: 0;

    font-weight: 400;
    font-size: 14px;
    color: #000;
  }
}
