.accordion__list {
  display: flex;
  flex-direction: column;
  gap: 15px;
}

.open .accordion__list {
  gap: 0px;
}

.accordion__control {
  padding: 30px;

  width: 100%;

  display: flex;
  align-items: center;
  justify-content: space-between;
  text-align: left;
  column-gap: 10px;

  border-radius: 20px;
  background-color: #fff;
  cursor: pointer;

  @include mobile {
    padding: 20px;
  }
}

.accordion__content {
  max-height: 0;
  opacity: 0;
  overflow: hidden;
  padding: 0 20px;
  will-change: max-height;
  color: #fff;
  transition: all 0.3s ease-out;
  box-sizing: content-box;
}

.accordion__title {
  font-weight: 600;
  font-size: 18px;
  color: #598be0;

  @media (max-width: 600px) {
    max-width: 350px;
    font-size: 16px;
  }

  @media (max-width: 490px) {
    max-width: 230px;
  }

  @media (max-width: 380px) {
    max-width: 200px;
    font-size: 14px;
  }
}

.accordion__icon {
  position: relative;
  display: inline-block;
  width: 54px;
  height: 54px;
  transition: transform 0.3s ease-out;

  @media (max-width: 600px) {
    width: 40px;
    height: 40px;
  }

  @media (max-width: 390px) {
    width: 30px;
    height: 30px;
  }
}

.accordion__icon::before {
  content: "";
  position: absolute;
  top: 50%;
  left: 50%;
  width: 50%;
  height: 3px;
  border-radius: 45px;
  background-color: #598be0;
  transform: translate(-50%, -50%);
}

.accordion__icon::after {
  content: "";
  position: absolute;
  top: 50%;
  left: 50%;
  width: 3px;
  height: 50%;
  border-radius: 45px;
  background-color: #598be0;
  transform: translate(-50%, -50%);
}

.open .accordion__icon {
  transform: rotate(45deg);
}

.open .accordion__content {
  opacity: 1;
  margin-top: 8px;
  padding: 20px;

  border: 2px solid rgba(255, 255, 255, 0.2);
  border-radius: 20px;
  background-color: #1a1a1a;
}

.accordion__text {
  max-width: 800px;
}
