.reviews {
  &__container {
  }

  &__subtitle {
    margin: 0;
    margin-bottom: 20px;
  }

  &__descr {
    margin: 0 auto;

    display: block;

    max-width: 720px;

    font-weight: 600;
    text-align: center;
    font-size: 20px;
    color: #747474;

    @include mobile {
      font-size: 16px;
    }
  }

  &__wrapper {
    margin-right: 15px;
    padding-left: max(15px, calc((100% - var(--content-width)) / 2));
  }

  &__content {
    margin: 60px 0 45px;
  }

  &__item {
    padding: 20px;

    border-radius: 10px;
    background-color: #eaebed;

    @media (max-width: 550px) {
      margin: 0 auto;

      max-width: fit-content;
    }
  }

  &__image {
  }

  &__content-inner {
    display: flex;
    justify-content: space-between;
    gap: 28px;

    @media (max-width: 1130px) {
      flex-direction: column;
      align-items: center;
    }
  }

  &__card {
    @media (max-width: 1130px) {
      width: 100% !important;
    }
  }

  &__card--white {
    width: 45%;

    border-radius: 10px;
    background-color: #eaebed;
  }

  &__card--black {
    width: 60%;

    border-radius: 10px;
    background-color: #222;
  }
}

.swiper {
}
.swiper-wrapper {
}
.swiper-slide {
  width: 330px;

  @include mobile {
    width: 290px;
  }
}

.card {
  &__text {
    margin: 0;
    padding: 24px 10px;

    font-weight: 400;
    font-size: 20px;
    text-align: center;
    color: #598be0;

    @include mobile {
      padding: 16px;

      font-weight: 600;
      font-size: 16px;
    }
  }
}
