.ticker {
  padding-top: 95px;

  min-height: 430px;
  background-color: #222;

  @include mobile {
    padding-top: 65px;

    min-height: 255px;
  }

  @media (max-width: 400px) {
    min-height: 150px;
  }
}
.scroller {
  width: 100%;
  background-color: rgba(255, 255, 255, 0.5);

  &__inner {
    padding-block: 1rem;
    display: flex;
    gap: 100px;

    @media (max-width: 400px) {
      padding-block: 0;
      gap: 25px;
    }
  }

  &__item {
  }

  &__card {
    display: flex;
    align-items: center;
    gap: 64px;

    @media (max-width: 400px) {
      gap: 15px;
    }
  }
}

.scroller--rotate {
  position: relative;
  left: -5vw;
  width: 110vw;
  background-color: #fff;
  transform: rotate(4deg);
  margin-bottom: 30px;

  @media (max-width: 400px) {
    margin-bottom: 10px;
  }
}

.card {
  &__image {
    width: 40px;
    height: 40px;

    @media (max-width: 400px) {
      width: 20px;
      height: 20px;
    }
  }

  &__text--ticker {
    font-weight: 400;
    font-size: 35px;
    text-align: center;
    color: #000;

    @media (max-width: 400px) {
      font-size: 14px;
    }
  }
}

.scroller[data-animated="true"] {
  overflow: hidden;
}

.scroller[data-animated="true"] .scroller__inner {
  width: max-content;
  flex-wrap: nowrap;
  animation: scroll var(--_animation-duration, 40s)
    var(--_animation-direction, forwards) linear infinite;
}

.scroller--rotate[data-animated="true"] .scroller__inner {
  width: max-content;
  flex-wrap: nowrap;
  animation: scroll var(--_animation-duration, 40s)
    var(--_animation-direction, reverse) linear infinite;
}

.scroller[data-direction="right"] {
  --_animation-direction: reverse;
}

.scroller[data-direction="left"] {
  --_animation-direction: forwards;
}

.scroller[data-speed="fast"] {
  --_animation-duration: 20s;
}

.scroller[data-speed="slow"] {
  --_animation-duration: 60s;
}

@keyframes scroll {
  to {
    transform: translate(calc(-50% - 50px));

    @media (max-width: 400px) {
      transform: translate(calc(-50% - 18px));
    }
  }
}
