.history {
  @media (max-width: 1100px) {
    display: none;
  }

  &__container {
  }

  &__subtitle {
    margin: 0;
  }

  &__content {
    position: relative;
    margin-top: 175px;
  }

  &__image--vector {
  }

  &__ellipse {
    position: absolute;
    width: 20px;
    height: 20px;
  }

  &__ellipse::before {
    content: "Это псевдоэлемент";
    position: absolute;
    top: 0;
    left: 0;
    display: block;
    width: 250px;
    white-space: pre-wrap;
  }

  &__ellipse--first {
    position: absolute;
    top: 7px;
    left: -1px;
  }

  &__ellipse--first::before {
    content: "25 мая. \A начало написания книги.";
    position: absolute;
    top: -72px;
    left: 0;
  }

  &__ellipse--second {
    top: -8px;
    left: 17.3%;
  }

  &__ellipse--second::before {
    content: "17 августа. \A конец написания и начало создания задачника.";
    position: absolute;
    top: 61px;
    left: 0;
  }

  &__ellipse--third {
    top: -8px;
    left: 32.3%;
  }

  &__ellipse--third::before {
    content: "С 1 по 7 сентября. \A создали 4 мощнейших видео урока.";
    position: absolute;
    top: -75px;
    left: 0;
  }

  &__ellipse--fourth {
    top: 3px;
    left: 44.6%;
  }

  &__ellipse--fourth::before {
    content: "10 сентября. \A отдали в печатный дом : книгу, задачник и сертификат.";
    position: absolute;
    top: 50px;
    left: 0;
  }

  &__ellipse--fifth {
    top: -8px;
    left: 62%;
  }

  &__ellipse--fifth::before {
    content: "25 сентября. \A составили 3 портфеля с полным объяснением сделок для : долгосрока, среднесрока и краткосрока.";
    position: absolute;
    top: -95px;
    left: 0;
    width: 335px;
  }

  &__ellipse--sixth {
    top: 7px;
    left: 84.5%;
  }

  &__ellipse--sixth::before {
    content: "16 октября. \A вы видите этот сайт и всю нашу подготовку.";
    position: absolute;
    top: 50px;
    left: 0;
    width: 180px;
  }
}
