.about {
  background-color: #f5f5f5;

  &__container {
  }

  &__subtitle {
    margin: 0;
    margin-bottom: 60px;
  }

  &__list {
    display: grid;
    grid-template-columns: repeat(4, 1fr);
    column-gap: 60px;
    row-gap: 50px;

    @media (max-width: 1024px) {
      margin: 0 auto;

      max-width: 600px;

      grid-template-columns: repeat(2, 1fr);
    }

    @media (max-width: 640px) {
      margin: 0 auto;

      grid-template-columns: repeat(1, 1fr);
    }

    @include mobile {
      row-gap: 30px;
    }
  }

  &__item {
    grid-column: 1 span;

    @media (max-width: 640px) {
      width: 100%;

      display: flex;
      justify-content: center;

      border-radius: 20px;
      background-color: #fff;
    }
  }

  &__item--image {
    position: relative;

    @media (max-width: 640px) {
      display: none;
    }
  }

  &__article {
    max-width: 273px;

    @media (max-width: 640px) {
      margin: 0 auto;
      padding: 25px 31px;

      display: flex;
      max-width: max-content;
      flex-direction: column;
      align-items: center;
      text-align: center;
    }
  }

  &__btn {
    padding: 18px 0;
    margin: 30px auto 0;

    width: 100%;
    max-width: 250px;

    display: flex;
    justify-content: center;

    border-radius: 10px;
    background-color: #598be0;
    font-weight: 600;
    font-size: 16px;
    color: #fff;

    @media (min-width: 640px) {
      display: none;
    }
  }
}

.article {
  &__number {
    max-width: 44px;

    margin-bottom: 12px;
    padding: 12px 18px;

    display: block;

    font-family: var(--second-family);
    font-weight: 600;
    font-size: 16px;
    color: #598be0;

    border-radius: 10px;
    background-color: #fff;

    @media (max-width: 640px) {
      background-color: #f5f5f5;
    }
  }

  &__name {
    margin: 0;
    margin-bottom: 5px;

    font-weight: 400;
    font-size: 20px;
    color: #000;
  }

  &__info {
    margin: 0;

    font-weight: 400;
    font-size: 16px;
    color: #000;
  }

  &__image {
  }

  &__image--top {
    position: absolute;
    top: -85px;

    @media (max-width: 1024px) {
      position: static;
      top: 0;
    }
  }
}

.image__none {
  display: none;

  @media (max-width: 640px) {
    display: block;
  }
}
